#blue-background {
  background-image: linear-gradient(to bottom, #19597b, #62b1dd);
  color: white;
  font-family: 'Ubuntu', sans-serif;
}

.reflectBelow	{ 
  -webkit-box-reflect: below 0px -webkit-gradient(linear, right top, right bottom, from(transparent), color-stop(40%, transparent), to(rgba(255, 255, 255, 0.1)));
}

.profile-photo {
  width: 600px;
  max-width: 100%;
}

#col-no-padding{
  padding-left: 0px;
  padding-right: 0px;
}

ul#ul-reduce-padding {
  padding-left: 20px;
}

h1.title-header {
  font-size: 4rem;
}
h3.title-badge{
  margin-top: 23px;
}

h1 span{
  font-size: 2rem;
}


h1.header-badge{
  color: aliceblue;
  background-image: linear-gradient(to right, #19597b, #62b1dd);
}

h1.academic-header{
  font-size: medium;
  font-weight: bolder;
  margin-bottom: 0px;
}
p.academic-center{
  font-size: small;
  margin-bottom: 0px;
  font-weight: bolder;
  color: #7d7777
}
p.academic-dates{
  font-size: small;
  margin-bottom: 10px;
}


h1.work-experience-header {
  font-size: x-large;
  font-weight: bolder;
  margin-bottom: 0px;
}

p.work-experience-company {
  font-size: large;
  margin-bottom: 0px;
  font-weight: bolder;
  color: #7d7777
}

p.work-experience-dates {
  font-size: medium;
  margin-bottom: 10px;
}

div.work-experience-content {
 margin-bottom: 20px; 
}

div.before-experience {
  background-color: #e3e5e5;
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 20px;
  padding-top: 20px;
}
div#col-before {
  margin-bottom: 20px;
}

p.sample-projects-uri {
  font-size: medium;
  color: #19597b;
}
span#external-link {
  font-size: xx-small;
  padding-left: 5px;
}

span.span-bold {
  font-weight: bold;
  padding-left: 10px;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
